.datepicker {
  border-radius: 4px;
  color: #000;
  &-inline {
    width: 220px;
  }
  direction: ltr;
  &&-rtl {
    direction: rtl;
    table tr td span {
      float: right;
    }
  }
  &-dropdown {
    top: 0;
    left: 0;
    padding: 28px 26px;

    .datepicker-months,
    .datepicker-years {
      width: 334px;

      table {
        width: 100%;

        thead {
          tr {
            th {
              color: #000;
            }

            &:last-child {
              display: block;

              .datepicker-switch {
                width: 200px;
              }
              .prev,
              .next {
                width: 30%;
              }
            }
            &:first-child th {
              padding-bottom: 10px;
            }
          }
        }

        tr {
          td, th {
            width: 100%;
          }
        }
      }
    }
    .datepicker-months,
    .datepicker-years {
      td {
        span {
          width: 23%;
        }
      }
    }
    // &:before {
    //   content: '';
    //   display: inline-block;
    //   border-left:   7px solid transparent;
    //   border-right:  7px solid transparent;
    //   border-bottom: 7px solid #ccc;
    //   border-top:    0;
    //   border-bottom-color: rgba(0,0,0,.2);
    //   position: absolute;
    // }
    // &:after {
    //   content: '';
    //   display: inline-block;
    //   border-left:   6px solid transparent;
    //   border-right:  6px solid transparent;
    //   border-bottom: 6px solid #fff;
    //   border-top:    0;
    //   position: absolute;
    // }
    &.datepicker-orient-left:before   { left: 6px; }
    &.datepicker-orient-left:after    { left: 7px; }
    &.datepicker-orient-right:before  { right: 6px; }
    &.datepicker-orient-right:after   { right: 7px; }
    &.datepicker-orient-top:before    { top: -7px; }
    &.datepicker-orient-top:after     { top: -6px; }
    &.datepicker-orient-bottom:before {
      bottom: -7px;
      border-bottom: 0;
      border-top:    7px solid #999;
    }
    &.datepicker-orient-bottom:after {
      bottom: -6px;
      border-bottom: 0;
      border-top:    6px solid #fff;
    }
  }
  > div {
    display: none;
  }
  &.days .datepicker-days,
  &.months .datepicker-months,
  &.years .datepicker-years {
    display: block;
  }
  table {
    margin: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    thead {
      tr:last-child {
        color: #fff;
      }
      tr {
        th {
          &.dow {
            height: 5px;
            width: 5px;
            padding: 0;
          }
        }
      }
    }
    tr {
      td, th {
        text-align: center;
        width: 30px;
        height: 30px;
        border-radius: 0;
        border: none;
        padding: 10px 12px;

        .active.day {
          background: #fcbc56;
        }
      }
    }
  }
  // Inline display inside a table presents some problems with
  // border and background colors.
  .table-striped & table tr {
    td, th {
      background-color: transparent;
    }
  }
  table tr td {
    &.day:hover,
    &.day.focused {
      background: @gray-lighter;
      color: #000;
      cursor: pointer;
    }
    &.old,
    &.new {
      color: @btn-link-disabled-color;
    }
    &.disabled,
    &.disabled:hover {
      background: none;
      color: @btn-link-disabled-color;
      cursor: default;
    }
    &.highlighted {
      background: @state-info-bg;
      border-radius: 0;
    }
    &.today,
    &.today:hover,
    &.today.disabled,
    &.today.disabled:hover {
      @today-bg: lighten(orange, 30%);
      .button-variant(#000, @today-bg, darken(@today-bg, 20%));
    }
    &.today:hover:hover { // Thank bootstrap 2.0 for this selector...
      // TODO: Bump min BS to 2.1, use @textColor in buttonBackground above
      color: #000;
    }
    &.today.active:hover {
      color: #fff;
    }
    &.range,
    &.range:hover,
    &.range.disabled,
    &.range.disabled:hover {
      background: @gray-lighter;
      border-radius: 0;
    }
    &.range.today,
    &.range.today:hover,
    &.range.today.disabled,
    &.range.today.disabled:hover {
      @today-bg: mix(orange, @gray-lighter, 50%);
      .button-variant(#000, @today-bg, darken(@today-bg, 20%));
      border-radius: 0;
    }
    &.selected,
    &.selected:hover,
    &.selected.disabled,
    &.selected.disabled:hover {
      .button-variant(#fff, @gray-light, @gray);
      text-shadow: 0 -1px 0 rgba(0,0,0,.25);
    }
    &.active,
    &.active:hover,
    &.active.disabled,
    &.active.disabled:hover {
      .button-variant(#000, #fcbc56, darken(#fcbc56, 20%));
      text-shadow: 0 -1px 0 rgba(0,0,0,.25);
    }
    span {
      display: block;
      width: 23%;
      height: 54px;
      line-height: 54px;
      float: left;
      margin: 1%;
      cursor: pointer;

      &:hover {
        background: @gray-lighter;
      }
      &.disabled,
      &.disabled:hover {
        background: none;
        color: @btn-link-disabled-color;
        cursor: default;
      }
      &.active,
      &.active:hover,
      &.active.disabled,
      &.active.disabled:hover {
        .button-variant(@btn-primary-color, @btn-primary-bg, @btn-primary-border);
        text-shadow: 0 -1px 0 rgba(0,0,0,.25);
      }
      &.old,
      &.new {
        color: @btn-link-disabled-color;
      }
    }
  }

  .datepicker-switch {
    width: 145px;
    font-family: "Gotham Black"
  }

  thead tr:first-child th,
  tfoot tr th {
    cursor: pointer;
    &:hover {
      background: @gray-lighter;
    }
  }

  // Basic styling for calendar-week cells
  .cw {
    font-size: 10px;
    width: 12px;
    padding: 0 2px 0 5px;
    vertical-align: middle;
  }
  thead tr:first-child .cw {
    cursor: default;
    background-color: transparent;
  }
}
.input-group.date .input-group-addon {
  cursor: pointer;
}
.input-daterange {
  width: 100%;
  input {
    text-align: center;
  }
  input:first-child {
    border-radius: 3px 0 0 3px;
  }
  input:last-child {
    border-radius: 0 3px 3px 0;
  }
  .input-group-addon {
    width: auto;
    min-width: 16px;
    padding: 4px 5px;
    font-weight: normal;
    line-height: @line-height-base;
    text-align: center;
    text-shadow: 0 1px 0 #fff;
    vertical-align: middle;
    background-color: @input-group-addon-bg;
    border: solid @input-group-addon-border-color;
    border-width: 1px 0;
    margin-left: -5px;
    margin-right: -5px;
  }
}
